import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CFormTextarea,
  CImage,
  CRow,
} from "@coreui/react";
import axios from "axios";
import React, { useCallback, useEffect, useState } from "react";
import { api, headers } from "src/views/api";

const AddProduct = () => {
  const [product, setProduct] = useState({
    name: "",
    description: "",
    price: "",
    discount: "",
    categories: [],
    stock: "",
  });
  const [categories, setCategoeies] = useState([]);
  const [updateMode, setUpdateMode] = useState(false);
  const [file, setFile] = useState("");
  const [fileData, setFileData] = useState("");
  const role = localStorage.role;
  const userId = localStorage.userId;

  const handleChange = (e) =>
    setProduct({ ...product, [e.target.name]: e.target.value });

  const handleUpload = useCallback(
    (id) => {
      console.log(fileData);
      if (fileData && id) {
        try {
          const bodyFormData = new FormData();
          bodyFormData.append("image", fileData);

          axios({
            method: "post",
            url: `${api}/api/product/upload/${id}`,
            data: bodyFormData,
            headers: {
              "Content-Type": "multipart/form-data",
              "x-auth-token": localStorage.token,
            },
          });
        } catch (error) {
          console.log(error?.response);
        }
      }
    },
    [fileData]
  );

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === "string") {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      product.price = fixNumbers(product.price);
      product.discount = fixNumbers(product.discount);
      product.stock = fixNumbers(product.stock);
      const hRef = window.location?.href?.indexOf("=");
      const productId = window.location?.href?.slice(hRef + 1);

      if (hRef !== -1) {
        const data = {
          name: product?.name,
          description: product?.description,
          price: product?.price,
          discount: product?.discount,
          categories: product?.categories,
          stock: product?.stock,
        };
        await axios.put(`${api}/api/product/${productId}`, data, {
          headers,
        });

        handleUpload(productId);
      } else {
        const response = await axios.post(`${api}/api/product`, product, {
          headers,
        });

        if (response?.status === 200) {
          handleUpload(response?.data?._id);
        }
      }
    } catch (error) {
      alert(error?.response?.data?.message);
    }
  };

  useEffect(async () => {
    const hRef = window.location?.href?.indexOf("=");
    const productId = window.location?.href?.slice(hRef + 1);

    if (hRef !== -1) {
      setUpdateMode(true);
      const response = await axios.get(`${api}/api/product/${productId}`);
      setProduct({
        name: response?.data?.name,
        description: response?.data?.description,
        price: response?.data?.price,
        discount: response?.data?.discount,
        categories: [response?.data?.categories?.[0]?._id],
        stock: response?.data?.stock,
      });
      setFile(
        `${api}/uploads/admin/product/${productId}/${
          response?.data?.images?.[response?.data?.images?.length - 1]
        }`
      );
    }
    if (role === "admin") {
      const response = await axios.get(`${api}/api/category/${userId}`);
      setCategoeies(response?.data);
    } else {
      const response = await axios.get(`${api}/api/category`);
      setCategoeies(response?.data);
    }
  }, []);

  return (
    <CContainer>
      <CRow>
        ‍‍
        <CCol>
          <CForm onSubmit={handleSubmit}>
            <div className="mb-3">
              <CFormLabel htmlFor="name">نام محصول</CFormLabel>
              <CFormInput
                type="text"
                id="name"
                placeholder="نام محصول را اینجا وارد کنید"
                name="name"
                onChange={handleChange}
                value={product.name}
              />
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="name">عکس محصول</CFormLabel>
              {file && !file?.includes("undefined") ? (
                <div>
                  <CImage fluid src={file} className="mb-3" />
                </div>
              ) : null}
              <div className="form-file">
                <input
                  type="file"
                  className="form-file-input"
                  id="customFile"
                  style={{ display: "none" }}
                  onChange={(e) => {
                    setFileData(e.target.files[0]);
                    setFile(URL.createObjectURL(e.target.files[0]));
                  }}
                />
                <label className="form-file-label" htmlFor="customFile">
                  <span className=" m-3">عکس محصول را انتخاب کنید</span>
                  <span className="btn btn-primary">کلیک کنید</span>
                </label>
              </div>
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="price">قیمت</CFormLabel>
              <CFormInput
                type="text"
                id="price"
                placeholder="قیمت محصول"
                name="price"
                onChange={handleChange}
                value={product.price}
              />
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="discount">درصد تخفیف</CFormLabel>
              <CFormInput
                type="text"
                id="discount"
                placeholder="درصد تخفیف محصول"
                name="discount"
                onChange={handleChange}
                value={product.discount}
              />
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="discount">دسته بندی محصول</CFormLabel>
              <CFormSelect
                options={[
                  "انتخاب دسته بندی محصول",
                  ...categories?.map((el) => {
                    return {
                      label: el?.name,
                      value: el?._id,
                    };
                  }),
                ]}
                value={product?.categories?.[0]}
                onChange={(e) =>
                  setProduct({ ...product, categories: [e.target.value] })
                }
              />
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="stock">موجودی در انبار</CFormLabel>
              <CFormInput
                type="text"
                id="stock"
                placeholder="تعداد محصول موجود در انبار"
                name="stock"
                onChange={handleChange}
                value={product.stock}
              />
            </div>
            <div className="mb-3">
              <CFormLabel htmlFor="productDescription">
                توضیحات محصول
              </CFormLabel>
              <CFormTextarea
                id="productDescription"
                rows="3"
                name="description"
                onChange={handleChange}
                value={product.description}
              ></CFormTextarea>
            </div>
            <div className="mb-3">
              <CButton color="primary" type="submit">
                {updateMode ? "ویرایش" : "افزودن"}
              </CButton>
            </div>
          </CForm>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default AddProduct;
