import { cilTrash } from "@coreui/icons";
import CIcon from "@coreui/icons-react";
import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormTextarea,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from "@coreui/react";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { api, headers } from "src/views/api";

const AddCategory = () => {
  const [categories, setCategoeies] = useState([]);
  const [category, setCategory] = useState({
    name: "",
    description: "",
  });
  const role = localStorage.role;
  const userId = localStorage.userId;

  useEffect(async () => {
    if (role === "admin") {
      const response = await axios.get(`${api}/api/category/${userId}`);
      setCategoeies(response?.data);
    } else {
      const response = await axios.get(`${api}/api/category`);
      setCategoeies(response?.data);
    }
  }, []);

  const handleChange = (e) =>
    setCategory({ ...category, [e.target.name]: e.target.value });

  const handleSumbit = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(`${api}/api/category`, category, {
        headers,
      });

      if (response?.status === 200) {
        if (role === "admin") {
          const response = await axios.get(`${api}/api/category/${userId}`);
          setCategoeies(response?.data);
          setCategory({
            name: "",
            description: "",
          });
        } else {
          const response = await axios.get(`${api}/api/category`);
          setCategoeies(response?.data);
          setCategory({
            name: "",
            description: "",
          });
        }
      }
    } catch (error) {
      alert(
        error?.response?.data?.message
          ? error?.response?.data?.message
          : "نام تکراری مجاز نمیباشد"
      );
    }
  };

  const handleDeleteCategory = async (id) => {
    const confirm = window.confirm("از حذف این دسته اطمینان دارید؟");

    if (confirm) {
      try {
        const response = await axios.delete(`${api}/api/category/${id}`, {
          headers,
        });

        if (response?.status === 200) {
          if (role === "admin") {
            const response = await axios.get(`${api}/api/category/${userId}`);
            setCategoeies(response?.data);
          } else {
            const response = await axios.get(`${api}/api/category`);
            setCategoeies(response?.data);
          }
        }
      } catch (error) {
        alert(error?.response?.data?.message);
      }
    }
  };

  return (
    <div>
      <CContainer>
        <CRow>
          <CCol sm={12} md={6}>
            <div>
              {categories?.length ? (
                <CTable hover>
                  <CTableHead>
                    <CTableRow>
                      <CTableHeaderCell scope="col">#</CTableHeaderCell>
                      <CTableHeaderCell scope="col">نام دسته</CTableHeaderCell>
                      <CTableHeaderCell scope="col">توضیحات</CTableHeaderCell>
                      <CTableHeaderCell scope="col">عملیات</CTableHeaderCell>
                    </CTableRow>
                  </CTableHead>
                  <CTableBody>
                    {categories?.map((el, i) => (
                      <CTableRow key={el?._id}>
                        <CTableHeaderCell scope="row">{i + 1}</CTableHeaderCell>
                        <CTableDataCell>{el?.name}</CTableDataCell>
                        <CTableDataCell>{el?.description}</CTableDataCell>
                        <CTableDataCell>
                          <CButton
                            color="danger"
                            onClick={() => handleDeleteCategory(el?._id)}
                          >
                            <CIcon icon={cilTrash} />
                          </CButton>
                        </CTableDataCell>
                      </CTableRow>
                    ))}
                  </CTableBody>
                </CTable>
              ) : (
                "دسته بندی یافت نشد"
              )}
            </div>
          </CCol>
          <CCol>
            <CForm>
              <div className="mb-3">
                <CFormLabel htmlFor="categoryName">نام دسته بندی</CFormLabel>
                <CFormInput
                  type="text"
                  id="categoryName"
                  placeholder="نام دسته بندی را اینجا وارد کنید"
                  name="name"
                  onChange={handleChange}
                  value={category.name}
                />
              </div>
              <div className="mb-3">
                <CFormLabel htmlFor="categoryDescription">
                  توضیحات دسته بندی
                </CFormLabel>
                <CFormTextarea
                  id="categoryDescription"
                  rows="3"
                  name="description"
                  onChange={handleChange}
                  value={category.description}
                ></CFormTextarea>
              </div>
              <div className="mb-3">
                <CButton color="primary" onClick={handleSumbit}>
                  افزودن
                </CButton>
              </div>
            </CForm>
          </CCol>
        </CRow>
      </CContainer>
    </div>
  );
};

export default AddCategory;
